body{
  background-color: #1f1e1e;
  color: white;
  overflow: hidden;
}

h1{
  text-align: center;
}

.todo-wrapper{
  background: #353434;
  padding: 2%;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0px 5px 7px black;

}


.todo-input{
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgb(78, 78, 78);
  padding-bottom: 25px;
  margin-bottom: 25px;
}

.todo-input-item{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 25px;
}

.todo-input-item label{
  font-weight: bold;
  margin-bottom: 10px;
}

.todo-input-item input{
  padding: 8px;
  border : none;
  width: 250px;
}

.todo-input-item input:focus{
  outline: 2px solid rgb(160, 252, 174)
}


.primaryBtn{
  background-color: rgb(90, 212, 108);
  color: white;
  border: none;
  border-radius: 0px;
  margin-top: 20px;
  padding: 10px;
  width: 60px;
  cursor: pointer;
}

.primaryBtn:hover{
  background-color: rgb(45, 234, 73);
}

.secondaryBtn{
  background-color: rgb(71, 71, 71);
  color: white;
  border: none;
  border-radius: 0px;
  margin-top: 20px;
  padding: 10px;
  width: 60px;
  cursor: pointer;
  width: fit-content;
}

.active{
  background-color: rgb(90, 212, 108);
}


.todo-list{
  display: flex;
  flex-direction: column;

}

.btn-area{
  margin-bottom: 15px;
}


.todo-list-item{
  background-color: #414040;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px;
  padding-bottom: 10px;
  padding-top: 10px;
  margin-bottom: 10px;
  box-shadow: 0px 3px 5px rgb(43, 42, 42);
}

.todo-list-item h3{
  font-size: 25px;
  color: rgb(90, 212, 108);
  font-weight: bold;
  margin: 0;
}

.todo-list-item p{
  font-size: 14px;
  color: rgb(161, 161, 161);
  margin-top: 8px;
}

.icon{
  font-size: 35px;
  cursor: pointer;

}

.icon:hover{
  color: red;
}

.check-icon{
  font-size: 25px;
  margin-left: 10px;
  color: rgb(90, 212, 108);
  cursor: pointer;
}

.check-icon:hover{
  background-color: rgb(45, 234, 73)
}


.edit-wrapper{
  background-color: #414040;
  padding: 10px;
  display: flex;
  flex-direction: column;

}

.edit-wrapper input, .edit-wrapper textarea{
  border: 1px solid lightgray;
  padding: 10px;
  margin: 5px;
  border-radius: 3px;
}

.edit-wrapper .primaryBtn{
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  font-size: 13px;
  width: fit-content;
}



